import React from 'react';
import { useSelector } from 'react-redux';


const OrderConfirmation = () => {
  const placedOrderDetails = useSelector(state => state.basket.placedOrderDetails);

  return (
    <div>
      <h1>Zamwienie zostao przyjte</h1>
      <h3>Zapisz poniszy kod aby w pniejszym czasie mc sprawdzi wyniki na naszej stronie</h3>
      <p>
        {placedOrderDetails && placedOrderDetails.accessCode}
      </p>
    </div>
  )
}

export default OrderConfirmation
